import {useState, useEffect} from "react";
import {Container, Row, Col} from "react-bootstrap";
import {ArrowRightCircle} from "react-bootstrap-icons";
import 'bootstrap/dist/css/bootstrap.min.css'
import headerImg from "../assets/img/header-img.svg"
import 'animate.css';
import TrackVisibility from "react-on-screen";

export const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const toRotate = ["Airplane Colision Probability"];
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(300 - Math.random() * 100);
    const period = 1500;

    useEffect(() => {
        let ticker = setInterval(() => {
            tick();
        }, delta)
        return () => {
            clearInterval(ticker)
        }
    }, [text])

    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);
        setText(updatedText);

        if (isDeleting) {
            setDelta(prevDelta => prevDelta * 3 / 5)
        }

        if (!isDeleting && updatedText === fullText) {
            setIsDeleting(true);
            setDelta(period);
        } else if (isDeleting && updatedText === '') {
            setIsDeleting(false);
            setLoopNum(loopNum + 1);
            setDelta(500);
        }

    }


    return (<section className={'banner'} id={'home'}>
        <Container>
            <Row className={'align-items-center'}>
                <Col xs={12} md={6} xl={7}>
                    <TrackVisibility>
                        {({isVisible}) => <div className={isVisible ? 'animate__animated animate__fadeIn' : ''}>

                            <h2 style={{color: 'whitesmoke'}}>{`Welcome to   `}
                                <span className={"wrap"}> {text} </span>
                            </h2>
                            <p style={{backgroundColor: '#10303d'}} className={''}>Airplane colision Probability 
                            ini adalah suatu aplikasi yang dapat mendeteksi kemungkinan bertabrakan pesawat. Aplikasi 
                            ini menggunakan pengambilan data ADS-B Secara live melalui OpenSky API. Perhitungan dilakukan
                            menggunakan standar ACAS (Airbone Colision Avoidance System), Dimana hasil perhitungan data
                            akan di tampilkan secara visual.
                            </p>
                            <button onClick={() => console.log('connect')}>
                                <a href={'#connect'}>
                                Let's connect <ArrowRightCircle size={25}/></a>
                            </button>
                        </div>}
                    </TrackVisibility>
                </Col>
                <Col xs={12} md={6} xl={5}>
                    < TrackVisibility>
                        {({isVisible}) => <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                            <img src={headerImg} alt="Header Img"/>
                        </div>}
                    </TrackVisibility>
                </Col>
            </Row>
        </Container>
    </section>)
}